*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  background-color: aliceblue;
}
div{
  width: 100%;
  height: 100vh;
  background: palevioletred;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
h1{
  color: white;
  background: transparent;
  text-shadow: 0 2px 2px black;
}
button{
  line-height: 24px;
  padding: 0 20px;
  background-color: palevioletred;
  color: white;
  border: 2px solid white;
  outline: none;
  border-radius: 5px;
  text-transform: uppercase;
  cursor: pointer;
}
input{
  width: 100%;
  padding: 10px 20px;
  border: none;
  outline: none;
  height: 4rem;
  margin: 2rem 0;
  text-align: center;
}